<template>
  <section
    class="modal fade"
    id="exampleModalCenter"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalCenterTitle"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalCenterTitle">新增產品</h5>
          <button
            type="button"
            @click="cleanTempData()"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <!----->
        <div class="modal-body">
          <form>
            <div class="form-row">
              <div class="col">
                名稱
                <input type="text" class="form-control" placeholder="名稱" v-model="tempData.title">
              </div>
              <div class="col">
                性別
                <select class="custom-select mr-sm-2" v-model="tempData.gender">
                  <option selected>請選擇...</option>
                  <option value="men">男性</option>
                  <option value="women">女性</option>
                </select>
              </div>
              <div class="col">
                種類
                <select class="custom-select mr-sm-2" v-model="tempData.category">
                  <option selected>請選擇...</option>
                  <option value="1">One</option>
                  <option value="2">Two</option>
                  <option value="3">Three</option>
                </select>
              </div>
              <div class="col">
                價格
                <input type="number" class="form-control" placeholder="價格">
              </div>
            </div>
            <button @click.prevent="addEmptyColor">新增樣式</button>
            <section class="form-row my-5" v-for="(color,index) in tempData.colors" :key="index">
              <div class="col">
                <input type="file" class="filelisten">
                <img class="imglisten">
              </div>
              <div class="col">
                <div class="form-row">
                  <div class="col">樣式編號{{index+1}}</div>
                  <div class="col">
                    <button @click.prevent="deleteColor(index)">刪除</button>
                  </div>
                </div>
                <div class="form-row">
                  <div class="col">
                    樣式色碼
                    <input
                      type="text"
                      class="form-control"
                      placeholder="色碼"
                      v-model="color.colorNum"
                    >
                  </div>
                  <div class="col">
                    樣式色碼
                    <input
                      type="text"
                      class="form-control"
                      placeholder="色碼"
                      v-model="color.colorNum"
                    >
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-check form-check-inline">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="inlineCheckbox1"
                      value="option1"
                    >
                    <label class="form-check-label" for="inlineCheckbox1">1</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="inlineCheckbox2"
                      value="option2"
                    >
                    <label class="form-check-label" for="inlineCheckbox2">2</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="inlineCheckbox3"
                      value="option3"
                      disabled
                    >
                    <label class="form-check-label" for="inlineCheckbox3">3 (disabled)</label>
                  </div>
                </div>
              </div>
            </section>
          </form>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-dismiss="modal"
            @click="cleanTempData()"
          >Close</button>
          <button type="button" class="btn btn-primary">Save changes</button>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      colorCounter: 0,
      tempData: {
        title: "",
        gender: "",
        category: "",
        sub_category: "",
        price: 0,
        colorImgNum: 1,
        introductionImgNum: 0,
        globalImg: [],
        colors: [
          {
            colorId: "",
            colorNum: "",
            colorName: "",
            sizes: [
              {
                sizeId: "",
                sizeName: "",
                sizeStock: ""
              }
            ]
          }
        ]
      },
      emptyColor: {
        colorId: "",
        colorNum: "",
        colorName: "",
        sizes: [
          {
            sizeId: "",
            sizeName: "",
            sizeStock: ""
          }
        ]
      },
      emptyData: {
        title: "",
        gender: "",
        category: "",
        sub_category: "",
        price: 0,
        colorImgNum: 1,
        introductionImgNum: 0,
        globalImg: [],
        colors: [
          {
            colorId: "",
            colorNum: "",
            colorName: "",
            sizes: [
              {
                sizeId: "",
                sizeName: "",
                sizeStock: ""
              }
            ]
          }
        ]
      }
    };
  },
  methods: {
    deleteColor(colorIndex) {
      const self = this;
      this.tempData.colors.splice(colorIndex, 1);
    },
    cleanTempData() {
      this.tempData = this.emptyData;
    },
    listenNewColor(counterIndex) {
     /* const self = this;
      this.$nextTick(() => {
        let fileInputNode = document.querySelectorAll(`.file_${counterIndex}`);
        let imgNode = document.querySelectorAll(`.img_${counterIndex}`);
        fileInputNode.forEach(addEventListener("change", function() {
          var file = fileInputNode.files[0];
          var reader = new FileReader();
          reader.onload = function(e) {
            imgNode.src = e.target.result;
          };
          reader.readAsDataURL(file);
        });
      });*/
    },
    addEmptyColor() {
      this.colorCounter += 1;
      this.tempData.colors.push(this.emptyColor);
      this.listenNewColor(this.colorCounter);
    },
    initListenColor(){

    }
  },
};
</script>
